<template>
  <div class="container py-5">
    <Spinner :class="{ 'fade-out': !isLoading }" />
    <h1 class="mt-5">最新動態</h1>
    <hr />
    <div class="row">
      <div class="col-md-6">
        <Newest :products="products" />
      </div>
      <div class="col-md-6">
        <NewestComments :comments="comments" />
      </div>
    </div>
  </div>
</template>

<script>
import Newest from '../components/Newest'
import NewestComments from '../components/NewestComments'
import indexAPI from '../apis/products'
import Spinner from '../components/Spinner'
import { Toast } from '../utils/helpers'

export default {
  name: 'Feeds',
  components: {
    Newest,
    NewestComments,
    Spinner
  },
  data() {
    return {
      products: [],
      comments: [],
      isLoading: false
    }
  },
  methods: {
    async fetchFeeds() {
      try {
        this.isLoading = true
        const res = await indexAPI.getFeeds()
        const { products, comments } = res.data
        this.products = products
        this.comments = comments
        this.isLoading = false
      } catch (err) {
        console.log(err)
        this.toastFire('error', '無法取得該頁取得資訊，請嘗試重啟')
        this.isLoading = false
      }
    },
    toastFire(action, text) {
      Toast.fire({
        icon: action,
        title: text,
        background: '#eee',
        showConfirmButton: false,
      })
    },
  },

  created() {
    this.fetchFeeds()
  },
}
</script>
