<template>
  <div class="card">
    <div class="card-header">
      最新評論
    </div>
    <ul class="card-body list-unstyled">
      <li v-for="comment in comments" :key="comment.id" class="media my-3">
        <a href="/products">
          <img class="img-thumbnail shadow-sm img__box mxw__80 mr-3" :src="comment.Product.image" />
        </a>
        <div class="media-body">
          <a href="/products/">
          {{comment.Product.name}}
          </a>
          <p>{{ comment.description }}</p>
          <small class="text-secondary">
            <small class="text-muted">by </small>
            <a href="/users/"> {{ comment.User.name }}</a>
            <small class="text-muted"> {{comment.createdAt | fromNow}}</small>
          </small>
        </div>
      </li>
      <hr />
    </ul>
  </div>
</template>

<script>
import { fromNowFilter } from '../utils/mixins'
export default {
  mixins: [fromNowFilter],
  props: {
    comments: {
      type: Array,
      required: true,
    },
  },
}
</script>

